body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td,
menu {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li,
menu li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.relation {
  position: relative;
}

.relation__offset {
  height: 0;
}

.relation__content {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

body {
  background: #ddd;
}

.wrapper {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 30px;
}

.hw2 {
  margin: 40px 0;
}

.hw2 .row .relation__offset {
  padding-top: 56.25%;
}

.hw2 .row .relation__content {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
}

.hw2 .big {
  background: #FFFD73;
  flex: 1 1 100%;
  width: 40%;
}

.hw2 .product {
  flex: 1 1 100%;
  width: 30%;
  display: flex;
  flex-direction: column;
}

.hw2 .product1 {
  order: -1;
}

.hw2 .productItem {
  flex: 1 1 50%;
}

.hw2 .productItem:hover {
  opacity: 0.8;
}

.hw2 .item1 {
  background: #73C0FF;
}

.hw2 .item2 {
  background: #7373FF;
}

.hw2 .item3 {
  background: #73C0FF;
}

.hw2 .item4 {
  background: #7373FF;
}

.profit .row {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -15px;
}

.profit .row .item {
  margin-left: 15px;
  margin-right: 15px;
  box-sizing: border-box;
  width: calc(33.333333333333336% - 30px);
  text-align: center;
  margin-top: 15px;
  margin-bottom: 15px;
}

@media screen and (max-width: 780px) {
  .hw2 .row .relation__offset {
    padding-top: 100%;
  }

  .hw2 .big {
    width: 70%;
    background: #00ff00;
  }

  .hw2 .product {
    flex: 1 1 50%;
  }

  .hw2 .product1 {
    order: 0;
  }

  .profit .row {
    justify-content: center;
  }

  .profit .row .item {
    width: calc(50% - 30px);
  }
}

@media screen and (max-width: 560px) {
  .hw2 .row .relation__offset {
    display: none;
  }

  .hw2 .row .relation__content {
    flex-wrap: nowrap;
    position: static;
  }

  .hw2 .big {
    width: auto;
  }

  .hw2 .product {
    width: auto;
  }

  .profit .row .item {
    width: calc(83.33333333333334% - 30px);
  }
}